@charset "UTF-8";
.faq .faq__top {
  height: 300px;
  position: relative;
}

.faq .faq__top .faq__top__title {
  color: #111;
  font-size: 3.8rem;
  font-weight: 400;
  margin: 0;
  position: absolute;
  top: calc(50% - 1.9rem);
}

.faq .container {
  width: 1120px;
}

.faq-inner {
  width: 80%;
  margin: 0 auto;
}

.faq-inner h3 {
  color: #000000;
  text-align: center;
  background: url(../images/under_bg_h3.jpg) repeat-x center center;
  font-size: 32px;
  margin-bottom: 40px;
  position: relative;
  padding: 1rem 2rem calc(1rem + 10px);
  background: #fff100;
}

.faq-inner h3 span {
  padding: 0 25px;
  display: inline-block;
}

.faq-inner h3:before {
  position: absolute;
  top: -8px;
  left: -8px;
  width: 100%;
  height: 100%;
  content: '';
  border: 4px solid #000;
}

.faq-inner .intro {
  margin-bottom: 60px;
}

.faq-inner .intro p {
  font-size: 1.4rem;
  line-height: 1.7;
}

.faq-inner .question {
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}

.faq-inner .question .question_outer {
  padding: 10px;
  margin-top: 15px auto 0 auto;
  position: relative;
  display: flex;
}

.faq-inner .question .question_outer .ques {
  font-size: 2rem;
  padding: 5px 5px;
  margin: 10px 0;
  box-sizing: border-box;
}

.faq-inner .question .question_inner {
  padding: 10px;
  background-color: white;
  position: relative;
  display: flex;
}

.faq-inner .question .question_inner .answer {
  font-size: 1.4rem;
  margin: 0 auto;
  letter-spacing: 0.1em;
  max-width: 90%;
  line-height: 1.7;
  color: white;
  color: black;
}

.faq-inner .grad-wrap + .grad-wrap {
  margin-top: 40px;
}

.faq-inner .grad-trigger.fa {
  margin-right: .5em;
}

.faq-inner .grad-item {
  position: relative;
  overflow: hidden;
  display: flex;
}

.faq-inner .grad-item.is-hide {
  height: 40px;
}

.faq-inner .grad-item::before {
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 40px;
  background: -webkit-linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 50%);
  background: linear-gradient(top, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.9) 50%);
}

.faq-inner .grad-trigger.is-show {
  bottom: -2em;
}

.faq-inner .grad-trigger.is-show::after {
  content: "閉じる";
}

.faq-inner .grad-trigger.is-show + .grad-item::before {
  display: none;
}

.icon {
  width: 4.375rem;
  height: 4.375rem;
  border-radius: 1000px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: 1.875rem;
  letter-spacing: 0;
  line-height: 1;
  margin-right: 3rem;
  flex: none;
}

.icon_q {
  background-color: #e45213;
}

.icon_a {
  background-color: #70c438;
}
